import { useEffect, useState } from "react";
import { apiUrl } from "config";
import qs from "qs";

import logo from "assets/img/logo.svg";
import { ReactComponent as LogoSvg } from 'assets/img/logo.svg';

const Test = function () {
    useEffect(() => {
        getData();
    }, []);
    const getData = () => {
        const obj = {
            name: "winne",
            like: 88,
            age: 18,
            hello: "你好"
        };
        console.log(qs.stringify(obj));
        fetch(`${apiUrl}/posts`).then(async (res) => {
            if (res.ok) {
                console.log(await res.json());
            }
        });
    };

    const login = () => {
        const param = {
            username: "winne",
            password: "123456"
        };
        fetch(`${apiUrl}/login`, {
            method: "POST",
            headers: {
                "Content-Type": "application/json"
            },
            body: JSON.stringify(param)
        }).then(async (res) => {
            if (res.ok) {
                console.log(await res.json());
            }
        });
    };
    return (
        <div className="test">
            <div>
                <LogoSvg width="60px" color="red"/>
                <img src={require('assets/img/logo.svg').default} alt="logo" />
                <button onClick={login}>登录</button>
                test445555557777
            </div>
        </div>
    );
};

export default Test;
